
/* loader */

.loader-container{
    width: 100%;
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5%;
}

.loader {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }

/* navbar */

nav .brand-logo img{
    width: 200px;
    height: 7.5vh;
    object-fit: cover;
}

nav  li > a{
    color: black;
    font-weight: 550;
}

nav i.drop-icon {
    font-size: 32px;
}

nav i.call-icon {
    font-size: 22px;
    color: var(--primaryColor);
}
 
@media(max-width:700px){
    nav {
        height: 8vh;
    }
}

#sidenav-image {
    width: 200px;
    height: 200px;
    object-fit: cover;
}

.sidenav h5 {
    margin-top: 0px;
    padding-top: 0px;
}

#destinationdropdown {
    width: 35%;
    padding: 15px;
}

#destinationdropdown h6 {
    font-weight: 750px;
    text-decoration: underline;
}

#destinationdropdown a {
    margin: 0px;
    background: transparent;
    color: black;
}

#destinationdropdown a:hover {
    text-decoration: underline;
}

#destinationdropdown .col {
    margin-top: 0px;
    padding-top: 0px;
}

