}

#destinationdropdown .col {
    margin-top: 0px;
    padding-top: 0px;
}



/* default carousel */

.default-caro {
    height: 80vh;
}

.default-caro > .carousel-item {
    width: 100%;
    height: 80vh;
}

.default-caro .caro-item {
    width: 100%;
    height: 80vh;
    background: var(--primaryColor);
    overflow: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: white;
}

.default-caro .caro-item h4 {
    font-family: cursive;
    font-weight: normal;
    font-size: 42px;
}

.default-caro .caro-item p {
    font-family: cursive;
    text-align: center;
    width: 25%;
    font-size: 18px;
}

#caro-1 {
    background: url('/images/caro1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

#caro-2 {
    background: url('/images/caro2.jpg');
    background-color: rgb(179, 179, 179);
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;    
}

#caro-3 {
    background: url('/images/caro3.jpg');
    background-color: rgb(179, 179, 179);
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;    
}

.caro-button {
    position: absolute;
    width: 40px;
    height: 100px;
    transform: translateY(-50%);
    background: rgb(53, 53, 53);
    outline: none;
    border: none;
    cursor: pointer;
    color: white;
    opacity: 0.5;
    transition: 0.2s opacity ease-in;
}

.caro-button:hover {
    opacity: 0.8;
}

.btn-right {
    top: 50%;
    right: 0;
}

.btn-left {
    top: 50%;
    left: 0;
}

@media(max-width: 700px){
    .default-caro > .carousel-item {
        width: 100%;
        height: 50vh;
    }

    .default-caro .caro-item {
        width: 100%;
        height: 100%;
        background: var(--primaryColor);
    }

    .default-caro .caro-item h4 {
        font-size: 24px;
        text-align: center;   
    }

    .default-caro .caro-item p {
        display: none;
    }

}


/* activity section  */

#activty_section {
    width: 100%;
    padding: 38px 0px;
    background:var(--secondaryColor);
    overflow: auto;    
    font-family: 'Roboto', sans-serif;
}

#activty_section h2{
    font-weight: bolder;
    font-size: 32px;
    margin: 0px;
    padding: 0px;
    color: white;
}

#search-icons {
    width: 100%;
    margin-top: 32px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
    flex-wrap: wrap;
}

.search-icon{
    height: 100px;
    width: 100px;
    border-radius: 50%;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    cursor: pointer;
    box-shadow: 3px 3px 20px rgba(255, 255, 255, 0.2);
}

.search-icon:hover {
    box-shadow: 3px 3px 20px rgba(255, 255, 255, 0.4);
}

.search-icon h5 {
    margin: 0px;
    padding: 0px;
    font-size: 14px;
    font-weight: bolder;
}

.search-icon img {
    width: 55px;
    height: 55px;
    margin-bottom: 5px;
}

@media(max-width:500px){
    .search-icon{
        margin-bottom: 24px;
    }

    #activty_section h2 {
        font-size: 28px;
    }

}


/* destinations */

#destinations h1{
    font-family: 'Roboto', sans-serif;
    margin: 32px auto;
    font-size: 38px;
    text-align: center;
    font-weight: bolder;
}

#destinations #grid {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4,1fr);
    grid-template-rows: 2;
    grid-template-areas: 
    "first first second second"
    "third fourth fifth sixth";
    grid-gap:10px;
    margin-bottom: 20vh;

}

.grid-item {
    background: url('/images/dest1.jpg');
    background-color: var(--blendColor);
    background-blend-mode: multiply;
    height: 40vh;
    cursor: pointer;
    transition: 0.3s transform ease-in-out;
    position: relative;
}

.grid-item:hover {
    transform: translateY(-5px);
}

.grid-item h2 {
    position: absolute;
    bottom: 5%;
    left: 5%;
    color:var(--background);
    font-size: 32px;
    font-family: 'Roboto', sans-serif;
    font-weight: bolder;
}

.grid-item:nth-child(1){
    grid-area: first;
    background-repeat: no-repeat;
}

.grid-item:nth-child(2){
    grid-area: second;
    background: url('/images/dest2.jpg');
    background-color:var(--blendColor);
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-size: cover;
}

.grid-item:nth-child(3){
    grid-area: third;
    background: url('/images/dest3.jpg');
    background-color: var(--blendColor);
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-size: cover;
}

.grid-item:nth-child(4){
    grid-area: fourth;
    background: url('/images/dest4.jpg');
    background-color: var(--blendColor);
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-size: cover;
}

.grid-item:nth-child(5){
    grid-area: fifth;
    background: url('/images/dest5.jpg');
    background-color: var(--blendColor);
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-size: cover;
}

.grid-item:nth-child(6){
    grid-area: sixth;
    background: url('/images/dest6.jpg');
    background-color: var(--blendColor);
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-size: cover;
}


@media(max-width:500px){

    #destinations{
        width: 100%;
        overflow-x: hidden;
    }

    #destinations #grid {
        width: 90%;
        grid-template-columns: repeat(4,80vw);
        grid-template-areas: 
        "first second third fourth fifth sixth";
        margin-bottom: 10vh;
    }

    .grid-item {
        height: 50vh;
    }

    .grid-item:hover {
        transform: none;
    }
}

/* subscribe */

#subscribe {
    width: 100%;
    height: 30vh;
    background:var(--secondaryColor);
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Roboto', sans-serif;
}

#subscribe h1 {
    margin: 0px;
    padding: 0px;
    font-family: 'Roboto', sans-serif;
    color:white;
    margin-right: 32px;
    font-size: 24px;
    font-weight: bolder;
}

#subscribe h4 {
    margin: 0px;
    padding: 0px;
    margin-top: 12px;
    color:white;
    font-size: 16px;
    font-weight: bolder;
}

#subscribe input {
    width: 25%;
    height: 3vh;
    padding:20px 30px;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    color: white;
    border-radius: 3px;
}

#subscribe button {
    margin: 0;
    margin-left: 24px;
    text-transform: capitalize;
    font-family: 'Roboto', sans-serif;
    font-size: 18px;    
}

#email_model {
    width: 30%;
}

@media(max-width:500px) {
    #subscribe{
        height: 40vh;
        flex-direction: column;
    }

    #subscribe h1,#subscribe h4,#subscribe input, #subscribe button{
        margin:0;
    }

    #subscribe h4 {
        margin-top: 5px;
    }    

    #subscribe input {
        margin:24px auto;
        width: 80%;
    }

    #subscribe button {
        width: 80%;
    }

    #email_model {
        width: 90%;
    }

}


/* pacakage card  */

#pacakges-container {
    width: 100%;
    height: auto;
    background: var(--background);
    padding:5vh 0px 10vh 0px;
}

#pacakges #data{
    display: flex;
    justify-content: space-between;
    margin-bottom: 10vh;
    align-items: center;
    font-family: 'Roboto', sans-serif;
}

#pacakges #data h2,#pacakges #data p{
    margin: 0px;
    padding: 0px;
}

#pacakges #data h2 {
    font-size: 24px;
    font-weight: bolder;
}

#pacakges #data p {
    margin-top: 5px;
    width: 80%;
}

#pacakges #data button {
    margin: 0px;
}

#pacakges #data a {
    display: block;
    font-weight: bolder;
    color:var(--secondaryColor);
}

#pacakges #button-group {
    display: none;
}

#pacakges #button-group button{
    width: 50px;
    padding:10px 5px;
    font-size: 16px;
}

#pacakges > div p{
    margin-top: 5px;
    color: var(--primaryColor);
}

#pacakges a {
    display: none;
}

.cards {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}



.card {
    margin-left: 3%;
    cursor: pointer;
    width: 30%;
    background: white;
    box-shadow: 3px 3px 15px rgba(0, 0, 0, 0.1);
    transition: box-shadow 0.3s ease-in-out;
    font-family: 'Roboto', sans-serif;
}

.card:hover {
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.2);
}

.card:nth-child(1),.card:nth-child(4){
    margin-left: 0px;
}

.card:nth-child(4),.card:nth-child(5),.card:nth-child(6) {
    margin-top: 3%;
}

.card img {
    width: 100%;
    height: 30vh;
    object-fit: cover;
}

.card .card-content {
    padding: 10px 20px;
}

.card .card-content h3 {
    margin: 0px;
    padding: 0px;
    font-size: 18px;
    font-weight: bolder;
    color: #333333;
}

.card p {
    margin: 0px;
    padding: 0px;
}

.card .card-content #price {
    font-size: 22px;
    color:var(--primaryColor);
    margin: 5px 0 10px 0;
}

.card #card-time{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.card #card-time p {
    margin:0;
    font-size: 12px;
    margin-left: 5px;
    color: var(--textSecondary);
}

.inclusions {
    margin-top: 5px;
    display: flex;
    flex-wrap: wrap;
}

.inclusions div {
    margin-left: 15px;
}

.inclusions div:nth-child(1),.inclusions div:nth-child(4){
    margin-left: 0px;
}

@media(max-width:500px) {
    #pacakges #data {
        margin-bottom: 5vh;
        text-align: center;
    }

    #pacakges #data p {
        width: 100%;
    }

    #pacakges #data a{
        display: none;
    } 
    #pacakges a {
        display: block;
        text-align: center;
        background: var(--secondaryColor);
        color:white;
        padding:5px 10px;
    }
    .cards {
        display: block;
    }
    .card {
        margin:0px;
        width: 100%;
        margin-bottom: 5vh;
    }
    .card:nth-child(4),.card:nth-child(5),.card:nth-child(6) {
        display: none;
    }
    .inclusions div {
        margin-left: 10px;
    }
}

/* testimonials */

#testimonials {
    width: 100%;
    background: var(--secondaryColor);
    padding:5vh 0px;
