#flight-model {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    height: auto;
    background: rgba(255,255,255,0.7);
    border-bottom: 10px solid var(--primaryColor);
}


#flight-model .row {
    margin-bottom: 0px;
}

#flight-model input[type="radio"] {
    font-size: 42px;
}

#flight-model span {
    font-size: 18px;
    font-family: 'Roboto', sans-serif;
    color: black;
}

#flight-model #options {
    display: flex;

}

#flight-model label {
    margin-right: 16px;
}

#flight-model-content {
    position: relative;
    padding: 18px;
}

#flight-iconBox {
    position: absolute;
    top: 0px;
    left: 0px;
    transform: translateY(-100%);
    background: rgba(255,255,255,0.9);
    padding: 5px 15px;
    display: flex;
    align-items: center;
    color: var(--primaryColor);
}

#flight-iconBox i {
    font-size: 32px;
}

#flight-iconBox span {
    margin-left: 12px;
    color: var(--primaryColor)
}

#flight-model-content input[type="text"]::-webkit-input-placeholder {
    color: black;
}

#flight-model-content input[type="text"]:-ms-input-placeholder {
    color: black;
}

#flight-model-content input[type="text"]::-ms-input-placeholder {
    color: black;
}

#flight-model-content input[type="text"]::placeholder {
    color: black;
}

#flight-model ul span {
    font-size: 14px;
}

[type="radio"]:checked+span:after, [type="radio"].with-gap:checked+span:before, [type="radio"].with-gap:checked+span:after {
    background-color: var(--primaryColor);
    border-color: white;
}

[type="radio"]+span:before, [type="radio"]+span:after {
    width: 20px;
    height: 20px;
}

.input-field {
    color: var(--primaryColor);
}

#flight-model .input-field .prefix {
    right: 0px;
}

.input-field .prefix.active{
    color: var(--primaryColor);
}

#flight-model .input-field .prefix ~ input {
    margin-left: 0px;
}

input[type="text"]:not(.browser-default):focus:not([readonly]){
    border-color: var(--primaryColor);
    box-shadow: 0px 1px 0px 0px var(--primaryColor);
    /* border: none; */
}

.datepicker-date-display {
    background-color: var(--primaryColor);
}

#flight-info {
    display: flex;
    align-items: center;
    padding: 22px 0px;
    padding-left: 10px;
    color: var(--primaryColor);
    cursor: pointer;
    transition: 0.3s background ease-in-out;
}

#flight-info:hover {
    background: white;
}

#flight-info span {
    margin-right: 12px;
}

#flight-info i {
    font-size: 28px;
}

#pass-info {
    width: 40%;
    font-family: 'Roboto', sans-serif;
    overflow: hidden;
}

#pass-wrapper {
    display: flex;
}

#pass-info #passengers {
    padding: 10px;
    width: 45%;
    border-right: 0.5px solid grey;
}

#pass-info #passengers #passengers-list {
    margin: 5px;
}

#passengers #passengers-list h6 {
    font-weight: bolder;
    font-size: 16px;
}

.val {
    margin-top: 5px;
}

.val span {
    margin: 10px;
}

#flight-class-info {
    width: 55%;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

#flight-class-info label {
    margin: 0px;
    margin-bottom: 5px;
}

#flight-class-info > div {
    margin-top: 12px;
}

#flight-class-info span {
    font-weight: bolder;
}

#flight-class-info #apply {
    margin-top: 8px;
}

#search {
    margin-top: 15px;
    padding: 0px 38px;
}

@media(max-width: 750px){

    .booking-caro {
        height: 100vh;
        background: url('/images/flight-caro1.jpg');
    }

    .hotel-caro {
        background: url('/images/hotel-caro2.jpg');
    }

    .tour-caro {
        background: url('/images/tour-caro2.jpg');
    }

    .event-caro {
        background: url('/images/event-caro2.jpg');
        height: 140vh;
    }

    .guide-caro {
        background: url('/images/guide-caro1.jpg');
        background-position: top;
    }
    
    .bus-caro {
        background: url('/images/bus-caro2.jpg');
    }

    #pass-info {
        width: 100%;
        transform: translateX(+5%);
    }

}

#user-info {
    width: 30%;
}

@media(max-width: 750px){
    #user-info {
        width: 90%;
        max-height: 80%;
    }
}

.learn-modal {
    color: black;
    width: 90%;
}

@media(min-width: 750px){

    .learn-modal {
        color: black;
        max-height: 80vh;
        top: -50% !important;
        transform: translateY(-50%);
    }

}
